import React from 'react';

const VotingButtons = ({ onUpvote, onDownvote, votes = 0, userVote = null }) => {
  return (
    <div className="flex items-center gap-2">
      <button
        onClick={onUpvote}
        className={`p-1 rounded ${userVote === 'up' ? 'text-green-600' : 'text-gray-500 hover:text-green-600'}`}
      >
        ▲
      </button>
      <span className="text-sm font-medium">{votes}</span>
      <button
        onClick={onDownvote}
        className={`p-1 rounded ${userVote === 'down' ? 'text-red-600' : 'text-gray-500 hover:text-red-600'}`}
      >
        ▼
      </button>
    </div>
  );
};

export default VotingButtons;