import React, { useEffect } from 'react';
import VideoEmbed from './VideoEmbed';
import MentionLink from './MentionLink';

const NewsContent = ({ content, className = '' }) => {
  // Load Twitter widgets script - must be called before any early returns
  useEffect(() => {
    if (content && (content.includes('twitter.com') || content.includes('x.com') || content.includes('[tweet:'))) {
      // Load Twitter widgets script if not already loaded
      if (!window.twttr) {
        const script = document.createElement('script');
        script.src = 'https://platform.twitter.com/widgets.js';
        script.async = true;
        script.charset = 'utf-8';
        document.head.appendChild(script);
      } else {
        // If already loaded, re-render widgets
        window.twttr.widgets.load();
      }
    }
  }, [content]);

  if (!content) return null;

  const parseContent = (text) => {
    const elements = [];
    let currentIndex = 0;

    // Combined regex for all patterns
    const patterns = [
      // Video embed patterns
      { 
        regex: /\[youtube:([^\]]+)\]/g, 
        type: 'youtube-embed',
        getProps: (match) => ({ type: 'youtube', id: match[1] })
      },
      { 
        regex: /\[twitch-clip:([^\]]+)\]/g, 
        type: 'twitch-clip-embed',
        getProps: (match) => ({ type: 'twitch-clip', id: match[1] })
      },
      { 
        regex: /\[twitch-video:([^\]]+)\]/g, 
        type: 'twitch-video-embed',
        getProps: (match) => ({ type: 'twitch-video', id: match[1] })
      },
      { 
        regex: /\[tweet:([^\]]+)\]/g, 
        type: 'tweet-embed',
        getProps: (match) => ({ type: 'tweet', id: match[1] })
      },
      // Direct URL patterns
      { 
        regex: /https?:\/\/(?:www\.)?(?:youtube\.com\/watch\?v=|youtu\.be\/)([^\s&\n]+)/g, 
        type: 'youtube-url',
        getProps: (match) => ({ type: 'youtube', url: match[0] })
      },
      { 
        regex: /https?:\/\/(?:www\.)?twitch\.tv\/[^\/]+\/clip\/([^\s\n]+)/g, 
        type: 'twitch-clip-url',
        getProps: (match) => ({ type: 'twitch-clip', url: match[0] })
      },
      { 
        regex: /https?:\/\/clips\.twitch\.tv\/([^\s\n]+)/g, 
        type: 'twitch-clip-url2',
        getProps: (match) => ({ type: 'twitch-clip', url: match[0] })
      },
      { 
        regex: /https?:\/\/(?:www\.)?twitch\.tv\/videos\/([^\s\n]+)/g, 
        type: 'twitch-video-url',
        getProps: (match) => ({ type: 'twitch-video', url: match[0] })
      },
      { 
        regex: /https?:\/\/(?:twitter\.com|x\.com)\/[^\/]+\/status\/([^\s\n]+)/g, 
        type: 'twitter-url',
        getProps: (match) => ({ type: 'tweet', url: match[0] })
      },
      // Mention patterns
      { 
        regex: /@(team|player|user):([^\s@\n]+)/g, 
        type: 'mention',
        getProps: (match) => ({ mentionType: match[1], username: match[2] })
      },
      { 
        regex: /@([^\s@\n]+)/g, 
        type: 'simple-mention',
        getProps: (match) => ({ username: match[1] })
      }
    ];

    // Find all matches
    const allMatches = [];
    patterns.forEach(pattern => {
      let match;
      while ((match = pattern.regex.exec(text)) !== null) {
        allMatches.push({
          ...pattern,
          match,
          start: match.index,
          end: match.index + match[0].length,
          text: match[0]
        });
      }
    });

    // Sort matches by position
    allMatches.sort((a, b) => a.start - b.start);

    // Process matches and build elements
    allMatches.forEach((matchObj, index) => {
      const { match, start, end, type, getProps } = matchObj;

      // Add text before this match
      if (start > currentIndex) {
        const textBefore = text.slice(currentIndex, start);
        if (textBefore.trim()) {
          elements.push(
            <span key={`text-${currentIndex}`} dangerouslySetInnerHTML={{ __html: formatText(textBefore) }} />
          );
        }
      }

      // Add the matched element
      const props = getProps(match);
      
      if (type.includes('embed') || type.includes('url')) {
        // Video or tweet embed
        elements.push(
          <VideoEmbed 
            key={`embed-${index}`} 
            {...props}
          />
        );
      } else if (type === 'mention' || type === 'simple-mention') {
        // Mention link
        elements.push(
          <MentionLink 
            key={`mention-${index}`}
            username={props.username}
            mentionType={props.mentionType}
            onClick={() => {
              // Handle mention click - could navigate to profile
              console.log('Mention clicked:', props);
            }}
          />
        );
      }

      currentIndex = end;
    });

    // Add remaining text
    if (currentIndex < text.length) {
      const remainingText = text.slice(currentIndex);
      if (remainingText.trim()) {
        elements.push(
          <span key={`text-${currentIndex}`} dangerouslySetInnerHTML={{ __html: formatText(remainingText) }} />
        );
      }
    }

    return elements.length > 0 ? elements : [
      <span key="full-text" dangerouslySetInnerHTML={{ __html: formatText(text) }} />
    ];
  };

  const formatText = (text) => {
    // Convert line breaks to paragraphs and preserve spacing
    return text
      .split('\n\n')
      .filter(para => para.trim())
      .map(para => `<p>${para.replace(/\n/g, '<br>')}</p>`)
      .join('');
  };

  const parsedContent = parseContent(content);

  return (
    <div className={`news-content prose prose-lg max-w-none dark:prose-invert ${className}`}>
      {parsedContent}
    </div>
  );
};

export default NewsContent;