import React from 'react';
import { TeamLogo } from '../utils/imageUtils';

const MatchCard = ({ match, onClick, className = '' }) => {
  if (!match) return null;

  const getStatusColor = (status) => {
    switch (status) {
      case 'live': return 'text-green-600';
      case 'upcoming': return 'text-blue-600';
      case 'completed': return 'text-gray-600';
      default: return 'text-gray-600';
    }
  };

  const getStatusBadge = (status) => {
    const colors = {
      'live': 'bg-green-100 text-green-800',
      'upcoming': 'bg-blue-100 text-blue-800',
      'completed': 'bg-gray-100 text-gray-800'
    };
    
    return (
      <span className={`px-2 py-1 rounded text-xs font-medium ${colors[status] || colors.upcoming}`}>
        {status?.toUpperCase() || 'TBD'}
      </span>
    );
  };

  const formatDate = (dateString) => {
    if (!dateString) return 'TBD';
    const date = new Date(dateString);
    return date.toLocaleDateString('en-US', { 
      month: 'short', 
      day: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    });
  };

  return (
    <div 
      onClick={onClick}
      className={`card cursor-pointer hover:shadow-lg transition-shadow ${className}`}
    >
      <div className="p-4">
        <div className="flex justify-between items-start mb-3">
          <div className="text-sm text-gray-500">
            {formatDate(match.scheduled_at)}
          </div>
          {getStatusBadge(match.status)}
        </div>
        
        <div className="flex items-center justify-between">
          <div className="flex items-center gap-3 flex-1">
            <TeamLogo team={match.team1} size="sm" />
            <div className="text-center">
              <div className="font-medium">{match.team1?.name || 'TBD'}</div>
            </div>
          </div>
          
          <div className="px-4">
            {match.status === 'completed' && match.series_score_team1 !== null ? (
              <div className="text-lg font-bold">
                {match.series_score_team1} - {match.series_score_team2}
              </div>
            ) : (
              <div className="text-gray-400">vs</div>
            )}
          </div>
          
          <div className="flex items-center gap-3 flex-1 justify-end">
            <div className="text-center">
              <div className="font-medium">{match.team2?.name || 'TBD'}</div>
            </div>
            <TeamLogo team={match.team2} size="sm" />
          </div>
        </div>
        
        {match.event && (
          <div className="mt-3 text-xs text-gray-500">
            {match.event.name}
          </div>
        )}
      </div>
    </div>
  );
};

export default MatchCard;