import React from 'react';

const MentionAutocomplete = ({ 
  value, 
  onChange, 
  placeholder = "Write your message...",
  className = '',
  ...props 
}) => {
  return (
    <textarea
      value={value}
      onChange={onChange}
      placeholder={placeholder}
      className={`w-full p-3 border border-gray-300 rounded-lg resize-none focus:outline-none focus:ring-2 focus:ring-red-500 ${className}`}
      {...props}
    />
  );
};

export default MentionAutocomplete;