import { useState, useRef, useEffect } from 'react';
import { useAuth } from './index';

export const useMentionAutocomplete = () => {
  const [showDropdown, setShowDropdown] = useState(false);
  const [mentionResults, setMentionResults] = useState([]);
  const [selectedIndex, setSelectedIndex] = useState(0);
  const [loading, setLoading] = useState(false);
  const [dropdownPosition, setDropdownPosition] = useState({ top: 0, left: 0 });
  const [mentionQuery, setMentionQuery] = useState('');
  const [mentionStart, setMentionStart] = useState(-1);
  
  const textareaRef = useRef(null);
  const dropdownRef = useRef(null);
  const searchTimeoutRef = useRef(null);
  
  const { api } = useAuth();

  // Mock data for development/fallback
  const mockResults = {
    team: [
      { id: 1, username: 'cloud9', name: 'Cloud9', type: 'team' },
      { id: 2, username: 'tsm', name: 'Team SoloMid', type: 'team' },
      { id: 3, username: 'liquid', name: 'Team Liquid', type: 'team' },
      { id: 4, username: 'fnatic', name: 'Fnatic', type: 'team' },
      { id: 5, username: 'g2', name: 'G2 Esports', type: 'team' }
    ],
    player: [
      { id: 1, username: 'faker', name: 'Lee Sang-hyeok', type: 'player' },
      { id: 2, username: 'caps', name: 'Rasmus Winther', type: 'player' },
      { id: 3, username: 'perkz', name: 'Luka Perković', type: 'player' },
      { id: 4, username: 'doublelift', name: 'Yiliang Peng', type: 'player' },
      { id: 5, username: 'bjergsen', name: 'Søren Bjerg', type: 'player' }
    ],
    user: [
      { id: 1, username: 'admin', name: 'Administrator', type: 'user' },
      { id: 2, username: 'moderator', name: 'Moderator', type: 'user' },
      { id: 3, username: 'editor', name: 'Content Editor', type: 'user' }
    ]
  };

  const searchMentions = async (query, type = null) => {
    if (!query || query.length < 1) {
      setMentionResults([]);
      return;
    }

    setLoading(true);

    try {
      let results = [];
      
      // If specific type is requested
      if (type && mockResults[type]) {
        results = mockResults[type].filter(item => 
          item.username.toLowerCase().includes(query.toLowerCase()) ||
          item.name.toLowerCase().includes(query.toLowerCase())
        );
      } else {
        // Search all types
        Object.keys(mockResults).forEach(entityType => {
          const filtered = mockResults[entityType]
            .filter(item => 
              item.username.toLowerCase().includes(query.toLowerCase()) ||
              item.name.toLowerCase().includes(query.toLowerCase())
            )
            .map(item => ({ ...item, type: entityType }));
          results = [...results, ...filtered];
        });
      }
      
      // Try to fetch from API if available
      try {
        if (api) {
          const response = await api.get(`/search/mentions?q=${encodeURIComponent(query)}${type ? `&type=${type}` : ''}`);
          if (response.data && Array.isArray(response.data)) {
            results = response.data;
          }
        }
      } catch (apiError) {
        console.log('API search not available, using mock data');
      }

      setMentionResults(results.slice(0, 10)); // Limit to 10 results
      setSelectedIndex(0);
    } catch (error) {
      console.error('Error searching mentions:', error);
      setMentionResults([]);
    } finally {
      setLoading(false);
    }
  };

  const calculateDropdownPosition = (textarea, cursorPosition) => {
    if (!textarea) return { top: 0, left: 0 };

    const textBeforeCursor = textarea.value.substring(0, cursorPosition);
    const lines = textBeforeCursor.split('\n');
    const currentLine = lines.length - 1;
    const currentColumn = lines[currentLine].length;

    const lineHeight = 20; // Approximate line height
    const charWidth = 8; // Approximate character width
    
    const top = (currentLine + 1) * lineHeight + 5;
    const left = currentColumn * charWidth;

    return { top, left };
  };

  const handleInputChange = (e, customValue = null) => {
    const textarea = e.target;
    const value = customValue !== null ? customValue : textarea.value;
    const cursorPosition = textarea.selectionStart;

    // Find mention pattern at cursor
    let mentionStart = -1;
    let mentionType = null;
    let mentionQuery = '';

    // Look for @type: pattern first
    const beforeCursor = value.substring(0, cursorPosition);
    const mentionPatternMatch = beforeCursor.match(/@(team|player|user):([^\s@]*)$/);
    
    if (mentionPatternMatch) {
      mentionStart = cursorPosition - mentionPatternMatch[0].length;
      mentionType = mentionPatternMatch[1];
      mentionQuery = mentionPatternMatch[2];
    } else {
      // Look for simple @ pattern
      const simpleMentionMatch = beforeCursor.match(/@([^\s@]*)$/);
      if (simpleMentionMatch) {
        mentionStart = cursorPosition - simpleMentionMatch[0].length;
        mentionQuery = simpleMentionMatch[1];
      }
    }

    if (mentionStart !== -1) {
      setMentionStart(mentionStart);
      setMentionQuery(mentionQuery);
      setShowDropdown(true);
      setDropdownPosition(calculateDropdownPosition(textarea, cursorPosition));
      
      // Debounce search
      if (searchTimeoutRef.current) {
        clearTimeout(searchTimeoutRef.current);
      }
      searchTimeoutRef.current = setTimeout(() => {
        searchMentions(mentionQuery, mentionType);
      }, 300);
    } else {
      setShowDropdown(false);
      setMentionResults([]);
      setMentionStart(-1);
    }
  };

  const handleKeyDown = (e, customValue = null) => {
    if (!showDropdown || mentionResults.length === 0) return {};

    switch (e.key) {
      case 'ArrowDown':
        e.preventDefault();
        setSelectedIndex(prev => 
          prev < mentionResults.length - 1 ? prev + 1 : 0
        );
        return { preventDefault: true };
        
      case 'ArrowUp':
        e.preventDefault();
        setSelectedIndex(prev => 
          prev > 0 ? prev - 1 : mentionResults.length - 1
        );
        return { preventDefault: true };
        
      case 'Enter':
      case 'Tab':
        e.preventDefault();
        const selectedMention = mentionResults[selectedIndex];
        if (selectedMention) {
          return { selectMention: selectedMention };
        }
        return { preventDefault: true };
        
      case 'Escape':
        setShowDropdown(false);
        setMentionResults([]);
        return { preventDefault: true };
        
      default:
        return {};
    }
  };

  const selectMention = (mention, setValue, currentValue) => {
    if (!mention || mentionStart === -1) return;

    const beforeMention = currentValue.substring(0, mentionStart);
    const afterMention = currentValue.substring(textareaRef.current?.selectionStart || mentionStart);
    
    const mentionText = mention.type ? `@${mention.type}:${mention.username}` : `@${mention.username}`;
    const newValue = beforeMention + mentionText + ' ' + afterMention;
    
    setValue(newValue);
    
    setShowDropdown(false);
    setMentionResults([]);
    setMentionStart(-1);
    
    // Set cursor position after the mention
    setTimeout(() => {
      if (textareaRef.current) {
        const newCursorPosition = beforeMention.length + mentionText.length + 1;
        textareaRef.current.setSelectionRange(newCursorPosition, newCursorPosition);
        textareaRef.current.focus();
      }
    }, 0);
  };

  // Clean up timeout on unmount
  useEffect(() => {
    return () => {
      if (searchTimeoutRef.current) {
        clearTimeout(searchTimeoutRef.current);
      }
    };
  }, []);

  return {
    textareaRef,
    dropdownRef,
    showDropdown,
    mentionResults,
    selectedIndex,
    loading,
    dropdownPosition,
    handleInputChange,
    handleKeyDown,
    selectMention
  };
};

export default useMentionAutocomplete;