import React, { useState, useEffect } from 'react';
import { useAuth } from '../../hooks';
import { TeamLogo, getCountryFlag, getImageUrl, getEventLogoUrl } from '../../utils/imageUtils';
// import MatchCard from '../MatchCard';
import SimpleBracket from '../SimpleBracket';

function EventDetailPage({ params, navigateTo }) {
  const [event, setEvent] = useState(null);
  const [matches, setMatches] = useState([]);
  const [teams, setTeams] = useState([]);
  const [bracket, setBracket] = useState(null);
  const [activeTab, setActiveTab] = useState('overview');
  const [loading, setLoading] = useState(true);
  const [showAddTeamModal, setShowAddTeamModal] = useState(false);
  const [availableTeams, setAvailableTeams] = useState([]);
  const [selectedTeamId, setSelectedTeamId] = useState('');
  const { api, isAdmin, isModerator } = useAuth();

  const eventId = params?.id;

  console.log('🔍 EventDetailPage - Received event ID:', eventId);

  useEffect(() => {
    if (eventId) {
      fetchEventData();
      fetchAvailableTeams();
    }
  }, [eventId]);

 // eslint-disable-line react-hooks/exhaustive-deps

  const fetchEventData = async () => {
    try {
      setLoading(true);
      console.log('🔄 EventDetailPage: Fetching event data for ID:', eventId);
      
      // Fetch comprehensive event data from API
      const response = await api.get(`/events/${eventId}`);
      const eventData = response.data?.data || response.data || response;
      
      console.log('✅ Real event data received:', eventData);
      
      // Set event data
      setEvent(eventData);
      
      // Set teams if included - check both nested and flat structure
      console.log('🏆 Teams data structure:', {
        'participation.teams': eventData.participation?.teams,
        'teams': eventData.teams,
        'hasParticipation': !!eventData.participation,
        'hasTeams': !!eventData.teams
      });
      
      if (eventData.participation?.teams && Array.isArray(eventData.participation.teams)) {
        console.log('✅ Using teams from participation:', eventData.participation.teams);
        setTeams(eventData.participation.teams);
      } else if (eventData.teams && Array.isArray(eventData.teams)) {
        console.log('✅ Using teams from root:', eventData.teams);
        setTeams(eventData.teams);
      } else {
        console.log('⚠️ No teams found in event data');
        setTeams([]);
      }
      
      // Set matches if included and transform flat structure to nested
      if (eventData.matches && Array.isArray(eventData.matches)) {
        const transformedMatches = eventData.matches.map(match => ({
          ...match,
          // Transform flat team data to nested structure
          team1: match.team1_name ? {
            id: match.team1_id,
            name: match.team1_name,
            short_name: match.team1_short,
            logo: match.team1_logo
          } : null,
          team2: match.team2_name ? {
            id: match.team2_id,
            name: match.team2_name,
            short_name: match.team2_short,
            logo: match.team2_logo
          } : null
        }));
        console.log('✅ Transformed matches:', transformedMatches);
        setMatches(transformedMatches);
      }
      
      // Set bracket if included
      if (eventData.bracket) {
        console.log('🎯 Setting bracket from event data:', eventData.bracket);
        setBracket(eventData.bracket);
      }

      // Fetch bracket data separately for all users
      try {
        console.log('🎯 Making bracket API call to:', `/events/${eventId}/bracket`);
        const bracketResponse = await api.get(`/events/${eventId}/bracket`);
        const bracketData = bracketResponse.data?.data || bracketResponse.data;
        console.log('🎯 Full bracket API response:', bracketResponse.data);
        console.log('🎯 Extracted bracket data:', bracketData);
        if (bracketData && bracketData.bracket) {
          console.log('✅ Setting bracket state from API:', bracketData.bracket);
          console.log('✅ Bracket rounds:', bracketData.bracket.rounds);
          setBracket(bracketData.bracket);
        } else {
          console.log('⚠️ No bracket data in response structure:', { bracketData, hasData: !!bracketData, hasBracket: !!(bracketData?.bracket) });
        }
      } catch (bracketError) {
        console.log('ℹ️ Bracket API error:', bracketError.message);
        console.log('ℹ️ No bracket data available yet');
      }
      
    } catch (error) {
      console.error('❌ Error fetching event data:', error);
      setEvent(null);
      setMatches([]);
      setTeams([]);
    } finally {
      setLoading(false);
    }
  };

  const fetchAvailableTeams = async () => {
    try {
      const response = await api.get('/teams');
      const allTeams = response.data?.data || response.data || [];
      setAvailableTeams(allTeams);
    } catch (error) {
      console.error('❌ Error fetching teams:', error);
    }
  };

  const handleAddTeam = async () => {
    if (!selectedTeamId) return;
    
    try {
      await api.post(`/admin/events/${eventId}/teams/${selectedTeamId}`);
      console.log('✅ Team added successfully');
      
      // Refresh event data
      await fetchEventData();
      setShowAddTeamModal(false);
      setSelectedTeamId('');
    } catch (error) {
      console.error('❌ Error adding team:', error);
      const errorMessage = error.response?.data?.message || error.message || 'Failed to add team';
      alert(errorMessage);
    }
  };

  const handleRemoveTeam = async (teamId) => {
    if (!window.confirm('Are you sure you want to remove this team from the event?')) return;
    
    try {
      await api.delete(`/admin/events/${eventId}/teams/${teamId}`);
      console.log('✅ Team removed successfully');
      
      // Refresh event data
      await fetchEventData();
    } catch (error) {
      console.error('❌ Error removing team:', error);
      const errorMessage = error.response?.data?.message || error.message || 'Failed to remove team';
      alert(errorMessage);
    }
  };

  const generateBracket = async () => {
    try {
      console.log('🏆 Generating bracket for event:', eventId);
      const response = await api.post(`/admin/events/${eventId}/generate-bracket`, {
        format: event.format || 'single_elimination',
        seeding_type: 'rating'
      });
      console.log('✅ Bracket generated successfully', response.data);
      
      // Use bracket data from response if available, otherwise refresh
      if (response.data?.data?.bracket) {
        setBracket(response.data.data.bracket);
      } else {
        await fetchEventData();
      }
    } catch (error) {
      console.error('❌ Error generating bracket:', error);
      alert('Failed to generate bracket: ' + (error.response?.data?.message || error.message));
    }
  };

  const handleBracketMatchUpdate = async (matchId, updates) => {
    try {
      await api.put(`/admin/matches/${matchId}`, updates);
      console.log('✅ Match updated successfully');
      
      // Refresh event data
      await fetchEventData();
    } catch (error) {
      console.error('❌ Error updating match:', error);
      alert('Failed to update match: ' + (error.response?.data?.message || error.message));
    }
  };

  const formatDate = (dateString) => {
    if (!dateString) return 'TBD';
    const date = new Date(dateString);
    return date.toLocaleDateString('en-US', { 
      year: 'numeric', 
      month: 'long', 
      day: 'numeric' 
    });
  };

  const formatCurrency = (amount, currency = 'USD') => {
    if (!amount || amount === 0) return 'TBD';
    return new Intl.NumberFormat('en-US', {
      style: 'currency',
      currency: currency
    }).format(amount);
  };

  const getStatusBadge = (status) => {
    const colors = {
      'upcoming': 'bg-blue-100 text-blue-800 dark:bg-blue-900/20 dark:text-blue-200',
      'ongoing': 'bg-green-100 text-green-800 dark:bg-green-900/20 dark:text-green-200',
      'completed': 'bg-gray-100 text-gray-800 dark:bg-gray-900/20 dark:text-gray-200',
      'cancelled': 'bg-red-100 text-red-800 dark:bg-red-900/20 dark:text-red-200'
    };
    
    return (
      <span className={`px-3 py-1 rounded-full text-sm font-medium ${colors[status] || colors.upcoming}`}>
        {status.charAt(0).toUpperCase() + status.slice(1)}
      </span>
    );
  };

  const tabs = [
    { id: 'overview', name: 'Overview' },
    { id: 'teams', name: `Teams (${teams.length})` },
    { id: 'matches', name: `Matches (${matches.length})` },
    { id: 'bracket', name: 'Bracket' }
  ];

  if (loading) {
    return (
      <div className="flex items-center justify-center py-12">
        <div className="text-center">
          <div className="loading-spinner mx-auto mb-4"></div>
          <div className="text-gray-600 dark:text-gray-400">Loading event details...</div>
        </div>
      </div>
    );
  }

  if (!event) {
    return (
      <div className="card p-12 text-center">
        <div className="text-6xl mb-4">🏆</div>
        <h2 className="text-2xl font-bold text-gray-900 dark:text-white mb-4">Event Not Found</h2>
        <p className="text-gray-600 dark:text-gray-400 mb-6">
          The event you're looking for doesn't exist or may have been removed.
        </p>
        <button 
          onClick={() => navigateTo && navigateTo('events')} 
          className="px-6 py-2 bg-red-600 text-white rounded hover:bg-red-700 transition-colors"
        >
          ← Back to Events
        </button>
      </div>
    );
  }

  return (
    <div className="container">
      {/* Simple breadcrumb */}
      <div style={{ marginBottom: '20px', fontSize: '13px' }}>
        <a 
          onClick={() => navigateTo && navigateTo('home')}
          className="text-link"
          style={{ cursor: 'pointer' }}
        >
          Main Page
        </a>
        <span> › </span>
        <a 
          onClick={() => navigateTo && navigateTo('events')}
          className="text-link"
          style={{ cursor: 'pointer' }}
        >
          Tournaments
        </a>
        <span> › </span>
        <span>{event.name}</span>
      </div>

      {/* Simple event header */}
      <div className="card" style={{ marginBottom: '20px' }}>
        <div className="p-16">
          <div style={{ display: 'flex', alignItems: 'flex-start', justifyContent: 'space-between', marginBottom: '16px' }}>
            <div>
              <h1 style={{ fontSize: '28px', fontWeight: 'bold', margin: '0 0 8px 0' }}>
                {event.name}
              </h1>
              <div style={{ marginBottom: '8px' }}>
                {event.status === 'upcoming' && <span className="status-upcoming">UPCOMING</span>}
                {event.status === 'ongoing' && <span className="status-live">ONGOING</span>}
                {event.status === 'completed' && <span className="status-completed">COMPLETED</span>}
                <span className="text-muted" style={{ marginLeft: '12px' }}>
                  {event.details?.region || event.region} • {event.details?.type || event.type}
                </span>
              </div>
            </div>
            
            {(isAdmin() || isModerator()) && (
              <button 
                onClick={() => navigateTo && navigateTo('admin-event-edit', { id: event.id })}
                className="btn btn-primary"
              >
                Edit Tournament
              </button>
            )}
          </div>
          
          {/* Simple event info */}
          <div style={{ display: 'grid', gridTemplateColumns: 'repeat(auto-fit, minmax(200px, 1fr))', gap: '16px' }}>
            <div>
              <div className="text-muted">Prize Pool</div>
              <div style={{ fontWeight: 'bold', color: 'var(--color-success)' }}>
                {formatCurrency(event.details?.prize_pool || event.prize_pool, event.details?.currency)}
              </div>
            </div>
            <div>
              <div className="text-muted">Start Date</div>
              <div>{formatDate(event.schedule?.start_date || event.start_date)}</div>
            </div>
            <div>
              <div className="text-muted">End Date</div>
              <div>{formatDate(event.schedule?.end_date || event.end_date)}</div>
            </div>
            <div>
              <div className="text-muted">Format</div>
              <div>{event.details?.format || event.format}</div>
            </div>
          </div>
        </div>
      </div>

      {/* Simple navigation */}
      <div style={{ marginBottom: '20px' }}>
        {tabs.map((tab, index) => (
          <a
            key={tab.id}
            onClick={() => setActiveTab(tab.id)}
            className={activeTab === tab.id ? 'nav-item active' : 'nav-item'}
            style={{ cursor: 'pointer', marginRight: index < tabs.length - 1 ? '20px' : '0' }}
          >
            {tab.name}
          </a>
        ))}
      </div>

      {/* Content sections */}
      {activeTab === 'overview' && (
        <div className="card">
          <div className="p-16">
            {event.description && (
              <div style={{ marginBottom: '20px' }}>
                <h3 className="section-header">About</h3>
                <p>{event.description}</p>
              </div>
            )}

            {event.rules && (
              <div style={{ marginBottom: '20px' }}>
                <h3 className="section-header">Rules</h3>
                <div>{event.rules}</div>
              </div>
            )}

            {event.details?.prize_distribution && (
              <div>
                <h3 className="section-header">Prize Distribution</h3>
                <table className="table">
                  <tbody>
                    {Object.entries(event.details.prize_distribution).map(([place, amount]) => (
                      <tr key={place}>
                        <td><strong>{place}</strong></td>
                        <td style={{ color: 'var(--color-success)', fontWeight: 'bold' }}>
                          {formatCurrency(amount, event.details?.currency)}
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            )}
          </div>
        </div>
      )}

      {/* Teams Tab */}
      {activeTab === 'teams' && (
        <div className="card">
          <div className="p-16">
            <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', marginBottom: '16px' }}>
              <h3 className="section-header" style={{ margin: 0 }}>
                Participating Teams ({teams.length}/{event.participation?.max_teams || event.max_teams})
              </h3>
              {(isAdmin() || isModerator()) && (
                <button 
                  onClick={() => setShowAddTeamModal(true)}
                  className="btn btn-primary"
                >
                  Add Team
                </button>
              )}
            </div>
            
            {teams.length > 0 ? (
              <table className="table">
                <thead>
                  <tr>
                    <th>Team</th>
                    <th>Region</th>
                    {(isAdmin() || isModerator()) && <th>Actions</th>}
                  </tr>
                </thead>
                <tbody>
                  {teams.map((team) => (
                    <tr key={team.id}>
                      <td>
                        <a 
                          onClick={() => navigateTo && navigateTo('team-detail', { id: team.id })}
                          className="text-link"
                          style={{ cursor: 'pointer', fontWeight: 'bold' }}
                        >
                          {team.name}
                        </a>
                      </td>
                      <td>
                        <span>{getCountryFlag(team.country)} {team.country || team.region}</span>
                      </td>
                      {(isAdmin() || isModerator()) && (
                        <td>
                          <button
                            onClick={() => handleRemoveTeam(team.id)}
                            className="btn-link"
                            style={{ color: 'var(--color-danger)' }}
                            title="Remove team"
                          >
                            Remove
                          </button>
                        </td>
                      )}
                    </tr>
                  ))}
                </tbody>
              </table>
            ) : (
              <div className="text-center p-16 text-muted">
                No teams registered yet
              </div>
            )}
          </div>
        </div>
      )}

      {/* Matches Tab */}
      {activeTab === 'matches' && (
        <div className="card">
          <div className="p-16">
            <h3 className="section-header">Tournament Matches</h3>
            {matches.length > 0 ? (
              <table className="table">
                <thead>
                  <tr>
                    <th>Match</th>
                    <th>Score</th>
                    <th>Status</th>
                  </tr>
                </thead>
                <tbody>
                  {matches.map(match => (
                    <tr key={match.id}>
                      <td>
                        <a 
                          onClick={() => navigateTo && navigateTo('match-detail', { id: match.id })}
                          className="text-link"
                          style={{ cursor: 'pointer', fontWeight: 'bold' }}
                        >
                          {match.team1?.name || 'TBD'} vs {match.team2?.name || 'TBD'}
                        </a>
                      </td>
                      <td>
                        {match.status === 'completed' ? (
                          <span style={{ fontWeight: 'bold' }}>
                            {match.series_score_team1 || 0}-{match.series_score_team2 || 0}
                          </span>
                        ) : (
                          <span className="text-muted">-</span>
                        )}
                      </td>
                      <td>
                        {match.status === 'live' && <span className="status-live">LIVE</span>}
                        {match.status === 'upcoming' && <span className="status-upcoming">UPCOMING</span>}
                        {match.status === 'completed' && <span className="status-completed">COMPLETED</span>}
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            ) : (
              <div className="text-center p-16 text-muted">
                No matches scheduled yet
              </div>
            )}
          </div>
        </div>
      )}

      {/* Bracket Tab */}
      {activeTab === 'bracket' && (
        <div className="card">
          <div className="p-16">
            <SimpleBracket 
              eventId={eventId}
              isAdmin={isAdmin() || isModerator()}
              navigateTo={navigateTo}
            />
          </div>
        </div>
      )}

      {/* Simple Add Team Modal */}
      {showAddTeamModal && (
        <div style={{
          position: 'fixed',
          top: 0,
          left: 0,
          right: 0,
          bottom: 0,
          backgroundColor: 'rgba(0,0,0,0.5)',
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          zIndex: 1000
        }}>
          <div className="card" style={{ width: '400px', padding: 0 }}>
            <div className="p-16">
              <h3 style={{ marginBottom: '16px', fontSize: '18px', fontWeight: 'bold' }}>Add Team to Tournament</h3>
              
              <select
                value={selectedTeamId}
                onChange={(e) => setSelectedTeamId(e.target.value)}
                className="form-input"
                style={{ width: '100%', marginBottom: '16px' }}
              >
                <option value="">Select a team</option>
                {availableTeams
                  .filter(team => !teams.some(t => t.id === team.id))
                  .map(team => (
                    <option key={team.id} value={team.id}>
                      {team.name} ({team.region})
                    </option>
                  ))
                }
              </select>
              
              <div style={{ display: 'flex', justifyContent: 'flex-end', gap: '12px' }}>
                <button
                  onClick={() => {
                    setShowAddTeamModal(false);
                    setSelectedTeamId('');
                  }}
                  className="btn btn-secondary"
                >
                  Cancel
                </button>
                <button
                  onClick={handleAddTeam}
                  disabled={!selectedTeamId}
                  className="btn btn-primary"
                  style={{ opacity: selectedTeamId ? 1 : 0.5 }}
                >
                  Add Team
                </button>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}


export default EventDetailPage;