import React from 'react';

const MentionLink = ({ username, mentionType, onClick, className = '' }) => {
  const getDisplayText = () => {
    if (mentionType) {
      return `@${mentionType}:${username}`;
    }
    return `@${username}`;
  };

  const getColorClass = () => {
    switch (mentionType) {
      case 'team':
        return 'text-green-600 hover:text-green-800 dark:text-green-400 dark:hover:text-green-300';
      case 'player':
        return 'text-purple-600 hover:text-purple-800 dark:text-purple-400 dark:hover:text-purple-300';
      case 'user':
        return 'text-blue-600 hover:text-blue-800 dark:text-blue-400 dark:hover:text-blue-300';
      default:
        return 'text-blue-600 hover:text-blue-800 dark:text-blue-400 dark:hover:text-blue-300';
    }
  };

  const handleClick = () => {
    if (onClick) {
      onClick({ username, mentionType });
    } else {
      // Default behavior - could navigate to profile page
      console.log('Mention clicked:', { username, mentionType });
    }
  };

  return (
    <span
      onClick={handleClick}
      className={`cursor-pointer font-medium transition-colors ${getColorClass()} ${className}`}
      title={`${mentionType ? mentionType.charAt(0).toUpperCase() + mentionType.slice(1) : 'User'}: ${username}`}
    >
      {getDisplayText()}
    </span>
  );
};

export default MentionLink;