import React, { useState, useEffect, useRef } from 'react';

function VideoEmbed({ type, id, url, mobileOptimized = true, lazyLoad = true }) {
  const [embedError, setEmbedError] = useState(false);
  const [embedUrl, setEmbedUrl] = useState('');
  const [isVisible, setIsVisible] = useState(!lazyLoad);
  const [isMobile, setIsMobile] = useState(window.innerWidth < 768);
  const videoRef = useRef(null);

  // Mobile/desktop detection
  useEffect(() => {
    const handleResize = () => {
      setIsMobile(window.innerWidth < 768);
    };
    
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  // Intersection Observer for lazy loading
  useEffect(() => {
    if (!lazyLoad || isVisible) return;

    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          setIsVisible(true);
          observer.disconnect();
        }
      },
      { threshold: 0.1, rootMargin: '50px' }
    );

    if (videoRef.current) {
      observer.observe(videoRef.current);
    }

    return () => observer.disconnect();
  }, [lazyLoad, isVisible]);

  useEffect(() => {
    if (!isVisible) return;
    
    // Parse URL if provided instead of ID
    if (url && !id) {
      const parsedId = parseVideoUrl(url, type);
      if (parsedId) {
        setEmbedUrl(getEmbedUrl(type, parsedId));
      } else {
        setEmbedError(true);
      }
    } else if (id) {
      setEmbedUrl(getEmbedUrl(type, id));
    }
  }, [type, id, url, isVisible]);

  const parseVideoUrl = (videoUrl, videoType) => {
    try {
      const urlObj = new URL(videoUrl);
      
      switch (videoType) {
        case 'youtube':
          // Handle youtube.com and youtu.be URLs
          if (urlObj.hostname.includes('youtube.com')) {
            return urlObj.searchParams.get('v');
          } else if (urlObj.hostname.includes('youtu.be')) {
            return urlObj.pathname.slice(1);
          }
          break;
          
        case 'twitch-clip':
          // Handle Twitch clip URLs
          if (urlObj.hostname.includes('twitch.tv') && urlObj.pathname.includes('/clip/')) {
            return urlObj.pathname.split('/clip/')[1];
          } else if (urlObj.hostname.includes('clips.twitch.tv')) {
            return urlObj.pathname.slice(1);
          }
          break;
          
        case 'twitch-video':
          // Handle Twitch video URLs
          if (urlObj.hostname.includes('twitch.tv') && urlObj.pathname.includes('/videos/')) {
            return urlObj.pathname.split('/videos/')[1];
          }
          break;
          
        case 'twitter':
        case 'tweet':
          // Handle Twitter/X URLs
          if ((urlObj.hostname.includes('twitter.com') || urlObj.hostname.includes('x.com')) && 
              urlObj.pathname.includes('/status/')) {
            return urlObj.pathname.split('/status/')[1].split('/')[0];
          }
          break;
      }
    } catch (e) {
      console.error('Error parsing video URL:', e);
    }
    return null;
  };

  const getEmbedUrl = (videoType, videoId) => {
    const mobileParams = isMobile ? '&playsinline=1&widget_referrer=' + encodeURIComponent(window.location.origin) : '';
    
    switch (videoType) {
      case 'youtube':
        return `https://www.youtube.com/embed/${videoId}?rel=0&modestbranding=1&controls=1&showinfo=0&fs=1${mobileParams}`;
        
      case 'twitch-clip':
        return `https://clips.twitch.tv/embed?clip=${videoId}&parent=${window.location.hostname}&autoplay=false&muted=${isMobile}`;
        
      case 'twitch-video':
        return `https://player.twitch.tv/?video=${videoId}&parent=${window.location.hostname}&autoplay=false&muted=${isMobile}`;
        
      case 'twitter':
      case 'tweet':
        // For Twitter, we'll render differently
        return null;
        
      default:
        return null;
    }
  };

  const handleIframeError = () => {
    setEmbedError(true);
  };

  // Load Twitter widgets for tweet embeds
  useEffect(() => {
    if ((type === 'twitter' || type === 'tweet') && (id || url) && window.twttr) {
      window.twttr.widgets.load();
    }
  }, [type, id, url]);

  // Twitter embed component
  if (type === 'twitter' || type === 'tweet') {
    const tweetId = id || parseVideoUrl(url, type);

    if (!tweetId || embedError) {
      return (
        <div className="bg-gray-100 dark:bg-gray-800 rounded-lg p-6 my-6 text-center">
          <div className="text-gray-500 dark:text-gray-400">
            <p className="mb-2">Tweet unavailable</p>
            {url && (
              <a 
                href={url} 
                target="_blank" 
                rel="noopener noreferrer"
                className="text-blue-500 hover:text-blue-600 text-sm"
              >
                View on X (Twitter) →
              </a>
            )}
          </div>
        </div>
      );
    }

    return (
      <div className="tweet-embed my-6 flex justify-center">
        <blockquote 
          className="twitter-tweet" 
          data-theme={document.documentElement.classList.contains('dark') ? 'dark' : 'light'}
        >
          <a href={`https://twitter.com/x/status/${tweetId}`}>Loading Tweet...</a>
        </blockquote>
      </div>
    );
  }

  // Video embed error state
  if (embedError || !embedUrl) {
    const videoTypeName = type === 'youtube' ? 'YouTube' : 
                         type.includes('twitch') ? 'Twitch' : 'Video';
    
    return (
      <div className="bg-gray-100 dark:bg-gray-800 rounded-lg p-8 my-6 text-center">
        <div className="text-gray-500 dark:text-gray-400">
          <div className="text-4xl mb-4">
            {type === 'youtube' ? '▶️' : '🎮'}
          </div>
          <p className="mb-2">{videoTypeName} content unavailable</p>
          {url && (
            <a 
              href={url} 
              target="_blank" 
              rel="noopener noreferrer"
              className="text-blue-500 hover:text-blue-600 text-sm"
            >
              Watch on {videoTypeName} →
            </a>
          )}
        </div>
      </div>
    );
  }

  // Lazy loading placeholder
  if (!isVisible) {
    return (
      <div 
        ref={videoRef}
        className={`video-embed ${type}-embed my-6 ${isMobile ? 'mobile-video-embed' : ''}`}
      >
        <div className="relative pb-[56.25%] h-0 bg-gray-200 dark:bg-gray-800 rounded-lg overflow-hidden shadow-xl flex items-center justify-center">
          <div className="absolute inset-0 flex flex-col items-center justify-center text-gray-500 dark:text-gray-400">
            <div className="text-4xl mb-4">
              {type === 'youtube' ? '▶️' : '🎮'}
            </div>
            <div className="text-sm font-medium">Loading video...</div>
            {isMobile && (
              <div className="text-xs mt-2 opacity-75">Tap to load</div>
            )}
          </div>
        </div>
      </div>
    );
  }

  // Video iframe embed
  return (
    <div 
      ref={videoRef}
      className={`video-embed ${type}-embed my-6 ${isMobile ? 'mobile-video-embed' : ''}`}
    >
      <div className={`relative ${isMobile ? 'pb-[177.78%] sm:pb-[56.25%]' : 'pb-[56.25%]'} h-0 bg-black rounded-lg overflow-hidden shadow-xl`}>
        {isMobile && mobileOptimized && (
          <div className="absolute top-2 right-2 z-10 bg-black bg-opacity-50 text-white px-2 py-1 rounded text-xs">
            📱 Mobile Optimized
          </div>
        )}
        <iframe
          src={embedUrl}
          title={`${type} video player`}
          frameBorder="0"
          allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
          allowFullScreen
          className="absolute top-0 left-0 w-full h-full"
          onError={handleIframeError}
          loading="lazy"
          style={{
            maxWidth: '100%',
            height: isMobile ? 'auto' : '100%'
          }}
        />
      </div>
      
      {/* Mobile controls hint */}
      {isMobile && (
        <div className="mt-2 text-center text-xs text-gray-500 dark:text-gray-400">
          Tap to play • Pinch to zoom • Double tap for fullscreen
        </div>
      )}
    </div>
  );
}

export default VideoEmbed;