import React, { useState, useEffect } from 'react';

function BulkOperationsPanel({ api }) {
  const [selectedItems, setSelectedItems] = useState([]);
  const [operationType, setOperationType] = useState('');
  const [availableData, setAvailableData] = useState({
    teams: { data: [], total: 0, current_page: 1, last_page: 1 },
    players: { data: [], total: 0, current_page: 1, last_page: 1 },
    matches: { data: [], total: 0, current_page: 1, last_page: 1 },
    events: { data: [], total: 0, current_page: 1, last_page: 1 }
  });
  const [loading, setLoading] = useState(false);
  const [progress, setProgress] = useState(0);
  const [currentPages, setCurrentPages] = useState({
    teams: 1,
    players: 1,
    matches: 1,
    events: 1
  });
  const [perPage] = useState(50); // Increased from 20 to 50 items per page
  const [searchTerms, setSearchTerms] = useState({
    teams: '',
    players: '',
    matches: '',
    events: ''
  });

  useEffect(() => {
    fetchAllData();
  }, []);

  const fetchDataForType = async (type, page = 1, search = '') => {
    try {
      const params = {
        page,
        per_page: perPage
      };
      
      if (search) {
        params.search = search;
      }

      const response = await api.get(`/${type}`, { params }).catch(() => ({ 
        data: { 
          data: [], 
          total: 0, 
          current_page: 1, 
          last_page: 1 
        } 
      }));

      // Handle both paginated and non-paginated responses
      const responseData = response.data;
      if (Array.isArray(responseData)) {
        // Non-paginated response, create pagination structure
        const startIndex = (page - 1) * perPage;
        const paginatedData = responseData.slice(startIndex, startIndex + perPage);
        return {
          data: paginatedData,
          total: responseData.length,
          current_page: page,
          last_page: Math.ceil(responseData.length / perPage)
        };
      } else if (responseData.data) {
        // Already paginated response
        return responseData;
      } else {
        return { data: [], total: 0, current_page: 1, last_page: 1 };
      }
    } catch (error) {
      console.error(`Error fetching ${type}:`, error);
      return { data: [], total: 0, current_page: 1, last_page: 1 };
    }
  };

  const fetchAllData = async () => {
    setLoading(true);
    try {
      const [teamsData, playersData, matchesData, eventsData] = await Promise.all([
        fetchDataForType('teams', currentPages.teams, searchTerms.teams),
        fetchDataForType('players', currentPages.players, searchTerms.players),
        fetchDataForType('matches', currentPages.matches, searchTerms.matches),
        fetchDataForType('events', currentPages.events, searchTerms.events)
      ]);

      setAvailableData({
        teams: teamsData,
        players: playersData,
        matches: matchesData,
        events: eventsData
      });
    } catch (error) {
      console.error('Error fetching bulk data:', error);
    } finally {
      setLoading(false);
    }
  };

  const handlePageChange = async (type, newPage) => {
    if (newPage < 1 || newPage > availableData[type].last_page) return;
    
    setCurrentPages(prev => ({ ...prev, [type]: newPage }));
    setLoading(true);
    
    try {
      const data = await fetchDataForType(type, newPage, searchTerms[type]);
      setAvailableData(prev => ({ ...prev, [type]: data }));
    } catch (error) {
      console.error(`Error fetching ${type} page ${newPage}:`, error);
    } finally {
      setLoading(false);
    }
  };

  const handleSearch = async (type, searchTerm) => {
    setSearchTerms(prev => ({ ...prev, [type]: searchTerm }));
    setCurrentPages(prev => ({ ...prev, [type]: 1 }));
    
    setLoading(true);
    try {
      const data = await fetchDataForType(type, 1, searchTerm);
      setAvailableData(prev => ({ ...prev, [type]: data }));
    } catch (error) {
      console.error(`Error searching ${type}:`, error);
    } finally {
      setLoading(false);
    }
  };

  const bulkOperations = [
    { id: 'delete', label: 'Delete Selected', icon: '', danger: true },
    { id: 'export', label: 'Export to CSV', icon: '', safe: true },
    { id: 'bulk-edit', label: 'Bulk Edit', icon: '', safe: true },
    { id: 'archive', label: 'Archive', icon: '', safe: true },
    { id: 'activate', label: 'Activate', icon: '', safe: true },
    { id: 'deactivate', label: 'Deactivate', icon: '', safe: true }
  ];

  const executeBulkOperation = async () => {
    if (!operationType || selectedItems.length === 0) {
      alert('Please select items and operation type');
      return;
    }

    const confirmMessage = `Are you sure you want to ${operationType} ${selectedItems.length} items?`;
    if (!window.confirm(confirmMessage)) return;

    setLoading(true);
    setProgress(0);

    try {
      const totalItems = selectedItems.length;
      const chunkSize = 100; // Process in chunks of 100 items
      
      // Skip export - handle separately
      if (operationType === 'export') {
        exportToCSV(selectedItems);
        setProgress(100);
        setLoading(false);
        return;
      }
      
      // Group items by type for bulk operations
      const itemsByType = selectedItems.reduce((acc, item) => {
        if (!acc[item.type]) acc[item.type] = [];
        acc[item.type].push(item.id);
        return acc;
      }, {});
      
      let processedItems = 0;
      
      // Execute bulk operation for each type in chunks
      for (const [type, ids] of Object.entries(itemsByType)) {
        // Process in chunks to avoid overwhelming the server
        for (let i = 0; i < ids.length; i += chunkSize) {
          const chunk = ids.slice(i, i + chunkSize);
          try {
            await api.post(`/admin/bulk/${type}s/${operationType}`, { ids: chunk });
            processedItems += chunk.length;
            setProgress((processedItems / totalItems) * 100);
          } catch (error) {
            console.error(`Error in bulk ${operationType} for ${type} chunk:`, error);
            // Continue processing other chunks even if one fails
          }
        }
      }
      
      setProgress(100);
      alert(`Bulk operation ${operationType} completed successfully!`);
      setSelectedItems([]);
      setOperationType('');
      await fetchAllData();
      
    } catch (error) {
      console.error('Bulk operation error:', error);
      alert(`Bulk operation failed: ${error.message}`);
    } finally {
      setLoading(false);
      setProgress(0);
    }
  };

  const exportToCSV = (data) => {
    const csvContent = "data:text/csv;charset=utf-8," 
      + "Type,ID,Name,Status\n"
      + data.map(item => `${item.type},${item.id},${item.name || item.title || 'N/A'},${item.status || 'active'}`).join("\n");
    
    const encodedUri = encodeURI(csvContent);
    const link = document.createElement("a");
    link.setAttribute("href", encodedUri);
    link.setAttribute("download", `mrvl_bulk_export_${new Date().toISOString().split('T')[0]}.csv`);
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  };

  const toggleItemSelection = (item, type) => {
    const itemWithType = { ...item, type };
    const isSelected = selectedItems.some(selected => selected.id === item.id && selected.type === type);
    
    if (isSelected) {
      setSelectedItems(prev => prev.filter(selected => !(selected.id === item.id && selected.type === type)));
    } else {
      setSelectedItems(prev => [...prev, itemWithType]);
    }
  };

  const selectAllOnPage = (type) => {
    const typeData = availableData[type]?.data || [];
    const typeItems = typeData.map(item => ({ ...item, type: type.slice(0, -1) }));
    
    const allSelected = typeItems.every(item => 
      selectedItems.some(selected => selected.id === item.id && selected.type === item.type)
    );
    
    if (allSelected) {
      // Deselect all items from this page
      setSelectedItems(prev => prev.filter(selected => 
        !typeItems.some(item => item.id === selected.id && item.type === selected.type)
      ));
    } else {
      // Select all items on this page
      const newSelections = typeItems.filter(item => 
        !selectedItems.some(selected => selected.id === item.id && selected.type === item.type)
      );
      setSelectedItems(prev => [...prev, ...newSelections]);
    }
  };

  const selectAllOfType = async (type) => {
    // This function will select ALL items of a type across all pages
    if (!window.confirm(`This will select ALL ${availableData[type].total} ${type}. Continue?`)) {
      return;
    }

    setLoading(true);
    try {
      // Fetch all pages for this type
      const allItems = [];
      const totalPages = availableData[type].last_page;
      
      for (let page = 1; page <= totalPages; page++) {
        const data = await fetchDataForType(type, page, searchTerms[type]);
        const items = data.data.map(item => ({ ...item, type: type.slice(0, -1) }));
        allItems.push(...items);
      }
      
      // Remove any existing selections of this type and add all new ones
      setSelectedItems(prev => [
        ...prev.filter(item => item.type !== type.slice(0, -1)),
        ...allItems
      ]);
      
      alert(`Selected all ${allItems.length} ${type}`);
    } catch (error) {
      console.error(`Error selecting all ${type}:`, error);
      alert(`Failed to select all ${type}`);
    } finally {
      setLoading(false);
    }
  };

  const renderPagination = (type) => {
    const data = availableData[type];
    if (!data || data.last_page <= 1) return null;

    const currentPage = currentPages[type];
    const lastPage = data.last_page;
    const maxButtons = 5;
    const halfButtons = Math.floor(maxButtons / 2);
    
    let startPage = Math.max(1, currentPage - halfButtons);
    let endPage = Math.min(lastPage, startPage + maxButtons - 1);
    
    if (endPage - startPage < maxButtons - 1) {
      startPage = Math.max(1, endPage - maxButtons + 1);
    }

    const pages = [];
    for (let i = startPage; i <= endPage; i++) {
      pages.push(i);
    }

    return (
      <div className="flex items-center justify-between mt-4 text-sm">
        <div className="text-gray-600 dark:text-gray-400">
          Page {currentPage} of {lastPage} • Total: {data.total} items
        </div>
        <div className="flex items-center space-x-1">
          <button
            onClick={() => handlePageChange(type, 1)}
            disabled={currentPage === 1}
            className="px-2 py-1 text-gray-600 dark:text-gray-400 hover:text-gray-900 dark:hover:text-white disabled:opacity-50 disabled:cursor-not-allowed"
          >
            ««
          </button>
          <button
            onClick={() => handlePageChange(type, currentPage - 1)}
            disabled={currentPage === 1}
            className="px-2 py-1 text-gray-600 dark:text-gray-400 hover:text-gray-900 dark:hover:text-white disabled:opacity-50 disabled:cursor-not-allowed"
          >
            «
          </button>
          {pages.map(page => (
            <button
              key={page}
              onClick={() => handlePageChange(type, page)}
              className={`px-3 py-1 rounded ${
                page === currentPage
                  ? 'bg-blue-600 text-white'
                  : 'text-gray-600 dark:text-gray-400 hover:bg-gray-200 dark:hover:bg-gray-700'
              }`}
            >
              {page}
            </button>
          ))}
          <button
            onClick={() => handlePageChange(type, currentPage + 1)}
            disabled={currentPage === lastPage}
            className="px-2 py-1 text-gray-600 dark:text-gray-400 hover:text-gray-900 dark:hover:text-white disabled:opacity-50 disabled:cursor-not-allowed"
          >
            »
          </button>
          <button
            onClick={() => handlePageChange(type, lastPage)}
            disabled={currentPage === lastPage}
            className="px-2 py-1 text-gray-600 dark:text-gray-400 hover:text-gray-900 dark:hover:text-white disabled:opacity-50 disabled:cursor-not-allowed"
          >
            »»
          </button>
        </div>
      </div>
    );
  };

  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        <div>
          <h2 className="text-3xl font-bold text-gray-900 dark:text-white">Bulk Operations</h2>
          <p className="text-gray-600 dark:text-gray-400">Manage multiple items efficiently</p>
        </div>
        <div className="text-sm text-gray-600 dark:text-gray-400">
          {selectedItems.length} items selected
        </div>
      </div>

      <div className="card p-6">
        <h3 className="text-lg font-bold text-gray-900 dark:text-white mb-4">Bulk Actions</h3>
        
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mb-6">
          <div>
            <label className="block text-sm font-medium mb-2 text-gray-900 dark:text-gray-100">
              Select Operation
            </label>
            <select
              value={operationType}
              onChange={(e) => setOperationType(e.target.value)}
              className="form-input"
            >
              <option value="">Choose operation...</option>
              {bulkOperations.map(op => (
                <option key={op.id} value={op.id}>
                  {op.label}
                </option>
              ))}
            </select>
          </div>
          
          <div className="flex items-end">
            <button
              onClick={executeBulkOperation}
              disabled={!operationType || selectedItems.length === 0 || loading}
              className={`btn w-full ${
                operationType && bulkOperations.find(op => op.id === operationType)?.danger
                  ? 'bg-red-600 hover:bg-red-700 text-white'
                  : 'bg-blue-600 hover:bg-blue-700 text-white'
              } disabled:opacity-50 disabled:cursor-not-allowed`}
            >
              {loading ? `Processing... ${Math.round(progress)}%` : 'Execute Operation'}
            </button>
          </div>
        </div>

        {loading && progress > 0 && (
          <div className="mb-4">
            <div className="bg-gray-200 dark:bg-gray-700 rounded-full h-2">
              <div 
                className="bg-blue-600 h-2 rounded-full transition-all duration-500"
                style={{ width: `${progress}%` }}
              ></div>
            </div>
          </div>
        )}
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {Object.entries(availableData).map(([type, dataInfo]) => {
          const items = dataInfo.data || [];
          const pageSelectedCount = items.filter(item => 
            selectedItems.some(selected => 
              selected.id === item.id && selected.type === type.slice(0, -1)
            )
          ).length;
          
          return (
            <div key={type} className="card p-6">
              <div className="mb-4">
                <div className="flex items-center justify-between mb-2">
                  <h3 className="text-lg font-bold text-gray-900 dark:text-white capitalize">
                    {type} ({dataInfo.total || 0} total)
                  </h3>
                  <div className="flex space-x-2">
                    <button
                      onClick={() => selectAllOnPage(type)}
                      className="text-sm text-blue-600 dark:text-blue-400 hover:text-blue-700"
                    >
                      {pageSelectedCount === items.length && items.length > 0 ? 'Deselect Page' : 'Select Page'}
                    </button>
                    {dataInfo.total > perPage && (
                      <button
                        onClick={() => selectAllOfType(type)}
                        className="text-sm text-purple-600 dark:text-purple-400 hover:text-purple-700"
                      >
                        Select All
                      </button>
                    )}
                  </div>
                </div>
                
                {/* Search bar for each type */}
                <input
                  type="text"
                  placeholder={`Search ${type}...`}
                  value={searchTerms[type]}
                  onChange={(e) => handleSearch(type, e.target.value)}
                  className="w-full px-3 py-2 mb-3 border border-gray-300 dark:border-gray-600 rounded-lg bg-white dark:bg-gray-800 text-gray-900 dark:text-white"
                />
              </div>
              
              <div className="space-y-2 max-h-96 overflow-y-auto">
                {items.map(item => {
                  const itemType = type.slice(0, -1);
                  const isSelected = selectedItems.some(selected => 
                    selected.id === item.id && selected.type === itemType
                  );
                  
                  return (
                    <div 
                      key={item.id}
                      className={`flex items-center space-x-3 p-3 rounded-lg cursor-pointer transition-colors ${
                        isSelected 
                          ? 'bg-blue-100 dark:bg-blue-900/30 border border-blue-300 dark:border-blue-700'
                          : 'bg-gray-50 dark:bg-gray-700 hover:bg-gray-100 dark:hover:bg-gray-600'
                      }`}
                      onClick={() => toggleItemSelection(item, itemType)}
                    >
                      <input
                        type="checkbox"
                        checked={isSelected}
                        onChange={() => {}}
                        className="text-blue-600"
                      />
                      <div className="flex-1">
                        <div className="font-medium text-gray-900 dark:text-white">
                          {item.name || item.title || `${itemType} #${item.id}`}
                        </div>
                        <div className="text-sm text-gray-600 dark:text-gray-400">
                          ID: {item.id} • Status: {item.status || 'active'}
                          {item.region && ` • Region: ${item.region}`}
                          {item.scheduled_at && ` • Date: ${new Date(item.scheduled_at).toLocaleDateString()}`}
                        </div>
                      </div>
                    </div>
                  );
                })}
                
                {items.length === 0 && (
                  <div className="text-center text-gray-600 dark:text-gray-400 py-8">
                    No {type} found
                  </div>
                )}
              </div>
              
              {renderPagination(type)}
            </div>
          );
        })}
      </div>

      <div className="card p-6">
        <h3 className="text-lg font-bold text-gray-900 dark:text-white mb-4">Quick Actions</h3>
        <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
          <button 
            onClick={() => setSelectedItems([])}
            className="btn btn-ghost"
          >
            Clear Selection
          </button>
          <button 
            onClick={() => exportToCSV(selectedItems)}
            disabled={selectedItems.length === 0}
            className="btn btn-ghost disabled:opacity-50"
          >
            Export Selected ({selectedItems.length})
          </button>
          <button 
            onClick={fetchAllData}
            className="btn btn-ghost"
          >
            Refresh Data
          </button>
          <button 
            onClick={() => {
              alert('Use "Select All" button in each section to select all items of that type');
            }}
            className="btn btn-ghost"
          >
            Help
          </button>
        </div>
      </div>
    </div>
  );
}

export default BulkOperationsPanel;