import React from 'react';

const HeroImage = ({ hero, size = 'md', className = '' }) => {
  const sizeClasses = {
    sm: 'w-8 h-8',
    md: 'w-12 h-12',
    lg: 'w-16 h-16',
    xl: 'w-20 h-20'
  };

  if (!hero) return null;

  const heroImage = hero.image_url || `/Heroes/${hero.name?.replace(/\s+/g, '_')}.jpg`;

  return (
    <img
      src={heroImage}
      alt={hero.name}
      className={`object-cover rounded ${sizeClasses[size]} ${className}`}
      onError={(e) => {
        e.target.src = '/images/default-placeholder.svg';
      }}
    />
  );
};

export default HeroImage;