import React, { useState, useRef } from 'react';
import NewsContent from '../shared/NewsContent';
import MentionDropdown from '../shared/MentionDropdown';
import { useMentionAutocomplete } from '../../hooks/useMentionAutocomplete';

const EnhancedContentEditor = ({ 
  value = '', 
  onChange, 
  placeholder = 'Write your content...',
  className = ''
}) => {
  const [isPreview, setIsPreview] = useState(false);
  const textareaRef = useRef(null);

  // Mention autocomplete
  const {
    textareaRef: mentionTextareaRef,
    dropdownRef,
    showDropdown,
    mentionResults,
    selectedIndex,
    loading: mentionLoading,
    dropdownPosition,
    handleInputChange: handleMentionInputChange,
    handleKeyDown: handleMentionKeyDown,
    selectMention
  } = useMentionAutocomplete();

  const handleChange = (e) => {
    const newValue = e.target.value;
    if (onChange) {
      onChange(newValue);
    }
    // Handle mention detection
    handleMentionInputChange(e);
  };

  const handleKeyDown = (e) => {
    const result = handleMentionKeyDown(e);
    if (result?.selectMention) {
      selectMention(result.selectMention, onChange, value);
    }
  };

  const insertVideoEmbed = (type) => {
    const embedText = `[${type}:VIDEO_ID_HERE]`;
    const textarea = mentionTextareaRef.current || textareaRef.current;
    
    if (textarea) {
      const start = textarea.selectionStart;
      const end = textarea.selectionEnd;
      const newValue = value.substring(0, start) + embedText + value.substring(end);
      
      if (onChange) {
        onChange(newValue);
      }
      
      // Set cursor position after the embed
      setTimeout(() => {
        textarea.focus();
        const newCursorPos = start + embedText.length;
        textarea.setSelectionRange(newCursorPos, newCursorPos);
      }, 0);
    }
  };

  const insertMention = (type) => {
    const mentionText = `@${type}:`;
    const textarea = mentionTextareaRef.current || textareaRef.current;
    
    if (textarea) {
      const start = textarea.selectionStart;
      const end = textarea.selectionEnd;
      const newValue = value.substring(0, start) + mentionText + value.substring(end);
      
      if (onChange) {
        onChange(newValue);
      }
      
      // Set cursor position after the mention prefix
      setTimeout(() => {
        textarea.focus();
        const newCursorPos = start + mentionText.length;
        textarea.setSelectionRange(newCursorPos, newCursorPos);
      }, 0);
    }
  };

  const detectAndReplaceURLs = (text) => {
    // Auto-convert pasted URLs to embed syntax
    return text
      .replace(/https?:\/\/(?:www\.)?(?:youtube\.com\/watch\?v=|youtu\.be\/)([^\s&\n]+)/g, '[youtube:$1]')
      .replace(/https?:\/\/(?:www\.)?twitch\.tv\/[^\/]+\/clip\/([^\s\n]+)/g, '[twitch-clip:$1]')
      .replace(/https?:\/\/clips\.twitch\.tv\/([^\s\n]+)/g, '[twitch-clip:$1]')
      .replace(/https?:\/\/(?:www\.)?twitch\.tv\/videos\/([^\s\n]+)/g, '[twitch-video:$1]')
      .replace(/https?:\/\/(?:twitter\.com|x\.com)\/[^\/]+\/status\/([^\s\n]+)/g, '[tweet:$1]');
  };

  const handlePaste = (e) => {
    const pastedText = e.clipboardData.getData('text');
    
    // Check if it's a URL that we can convert
    if (pastedText.match(/https?:\/\//)) {
      const convertedText = detectAndReplaceURLs(pastedText);
      if (convertedText !== pastedText) {
        e.preventDefault();
        const textarea = mentionTextareaRef.current || textareaRef.current;
        const start = textarea.selectionStart;
        const end = textarea.selectionEnd;
        const newValue = value.substring(0, start) + convertedText + value.substring(end);
        
        if (onChange) {
          onChange(newValue);
        }
        
        setTimeout(() => {
          textarea.focus();
          const newCursorPos = start + convertedText.length;
          textarea.setSelectionRange(newCursorPos, newCursorPos);
        }, 0);
      }
    }
  };

  return (
    <div className={`border border-gray-300 dark:border-gray-600 rounded-lg ${className}`}>
      {/* Toolbar */}
      <div className="flex items-center justify-between p-2 border-b border-gray-300 dark:border-gray-600 bg-gray-50 dark:bg-gray-800">
        <div className="flex items-center gap-2">
          <button
            type="button"
            onClick={() => setIsPreview(false)}
            className={`px-3 py-1 text-sm rounded transition-colors ${
              !isPreview 
                ? 'bg-blue-500 text-white' 
                : 'text-gray-600 dark:text-gray-300 hover:bg-gray-200 dark:hover:bg-gray-700'
            }`}
          >
            Edit
          </button>
          <button
            type="button"
            onClick={() => setIsPreview(true)}
            className={`px-3 py-1 text-sm rounded transition-colors ${
              isPreview 
                ? 'bg-blue-500 text-white' 
                : 'text-gray-600 dark:text-gray-300 hover:bg-gray-200 dark:hover:bg-gray-700'
            }`}
          >
            Preview
          </button>
        </div>
        
        {!isPreview && (
          <div className="flex items-center gap-2">
            {/* Video Embed Buttons */}
            <div className="flex items-center gap-1 border-l border-gray-300 dark:border-gray-600 pl-2">
              <button
                type="button"
                onClick={() => insertVideoEmbed('youtube')}
                className="px-2 py-1 text-xs bg-red-600 text-white rounded hover:bg-red-700 transition-colors"
                title="Insert YouTube embed"
              >
                📺 YouTube
              </button>
              <button
                type="button"
                onClick={() => insertVideoEmbed('twitch-clip')}
                className="px-2 py-1 text-xs bg-purple-600 text-white rounded hover:bg-purple-700 transition-colors"
                title="Insert Twitch clip embed"
              >
                🎮 Clip
              </button>
              <button
                type="button"
                onClick={() => insertVideoEmbed('twitch-video')}
                className="px-2 py-1 text-xs bg-purple-600 text-white rounded hover:bg-purple-700 transition-colors"
                title="Insert Twitch video embed"
              >
                🎮 Video
              </button>
              <button
                type="button"
                onClick={() => insertVideoEmbed('tweet')}
                className="px-2 py-1 text-xs bg-blue-600 text-white rounded hover:bg-blue-700 transition-colors"
                title="Insert Tweet embed"
              >
                🐦 Tweet
              </button>
            </div>
            
            {/* Mention Buttons */}
            <div className="flex items-center gap-1 border-l border-gray-300 dark:border-gray-600 pl-2">
              <button
                type="button"
                onClick={() => insertMention('team')}
                className="px-2 py-1 text-xs bg-green-600 text-white rounded hover:bg-green-700 transition-colors"
                title="Mention team"
              >
                🛡️ Team
              </button>
              <button
                type="button"
                onClick={() => insertMention('player')}
                className="px-2 py-1 text-xs bg-purple-600 text-white rounded hover:bg-purple-700 transition-colors"
                title="Mention player"
              >
                👤 Player
              </button>
              <button
                type="button"
                onClick={() => insertMention('user')}
                className="px-2 py-1 text-xs bg-blue-600 text-white rounded hover:bg-blue-700 transition-colors"
                title="Mention user"
              >
                💬 User
              </button>
            </div>
          </div>
        )}
      </div>

      {/* Content */}
      <div className="p-3 relative">
        {isPreview ? (
          <div className="min-h-[300px]">
            {value ? (
              <NewsContent content={value} />
            ) : (
              <div className="text-gray-500 dark:text-gray-400 italic">
                Nothing to preview. Write some content in the editor.
              </div>
            )}
          </div>
        ) : (
          <div className="relative">
            <textarea
              ref={(el) => {
                textareaRef.current = el;
                mentionTextareaRef.current = el;
              }}
              value={value}
              onChange={handleChange}
              onKeyDown={handleKeyDown}
              onPaste={handlePaste}
              placeholder={placeholder}
              className="w-full min-h-[300px] resize-y border-none outline-none bg-transparent text-gray-900 dark:text-gray-100"
            />
            
            {/* Mention Dropdown */}
            <MentionDropdown
              show={showDropdown}
              results={mentionResults}
              selectedIndex={selectedIndex}
              loading={mentionLoading}
              position={dropdownPosition}
              onSelect={(mention) => selectMention(mention, onChange, value)}
              dropdownRef={dropdownRef}
            />
          </div>
        )}
      </div>
      
      {!isPreview && (
        <div className="px-3 pb-2 text-xs text-gray-500 dark:text-gray-400 border-t border-gray-200 dark:border-gray-700 pt-2">
          <div className="flex flex-wrap gap-x-4 gap-y-1">
            <span>📝 Type @ to mention teams, players, or users</span>
            <span>📺 Paste YouTube/Twitch/Twitter URLs to auto-convert them</span>
            <span>⌨️ Use toolbar buttons to insert embeds and mentions</span>
          </div>
        </div>
      )}
    </div>
  );
};

export default EnhancedContentEditor;