import React, { useEffect } from 'react';

const MentionDropdown = ({ 
  show = false,
  results = [], 
  selectedIndex = 0,
  loading = false,
  position = { top: 0, left: 0 },
  onSelect, 
  dropdownRef,
  className = '' 
}) => {
  // Scroll selected item into view
  useEffect(() => {
    if (dropdownRef.current && selectedIndex >= 0) {
      const selectedElement = dropdownRef.current.children[selectedIndex];
      if (selectedElement) {
        selectedElement.scrollIntoView({ block: 'nearest' });
      }
    }
  }, [selectedIndex, dropdownRef]);

  if (!show) return null;

  const getTypeIcon = (type) => {
    switch (type) {
      case 'team': return '🛡️';
      case 'player': return '👤';
      case 'user': return '💬';
      default: return '👤';
    }
  };

  const getTypeColor = (type) => {
    switch (type) {
      case 'team': return 'text-green-600';
      case 'player': return 'text-purple-600';
      case 'user': return 'text-blue-600';
      default: return 'text-gray-600';
    }
  };

  return (
    <div 
      ref={dropdownRef}
      className={`absolute z-50 bg-white dark:bg-gray-800 border border-gray-300 dark:border-gray-600 rounded-lg shadow-xl max-h-48 overflow-y-auto ${className}`}
      style={{ 
        top: position.top, 
        left: position.left,
        minWidth: '250px'
      }}
    >
      {loading ? (
        <div className="px-3 py-2 text-center text-gray-500 dark:text-gray-400">
          <div className="flex items-center justify-center gap-2">
            <div className="w-4 h-4 border-2 border-gray-300 border-t-blue-500 rounded-full animate-spin"></div>
            Searching...
          </div>
        </div>
      ) : results.length > 0 ? (
        results.map((result, index) => (
          <div
            key={`${result.type}-${result.id}`}
            onClick={() => onSelect(result)}
            className={`px-3 py-2 cursor-pointer flex items-center gap-3 transition-colors ${
              index === selectedIndex 
                ? 'bg-blue-50 dark:bg-blue-900/30 border-l-2 border-blue-500' 
                : 'hover:bg-gray-50 dark:hover:bg-gray-700'
            }`}
          >
            <span className="text-lg">{getTypeIcon(result.type)}</span>
            <div className="flex-1 min-w-0">
              <div className="flex items-center gap-2">
                <span className={`font-medium ${getTypeColor(result.type)}`}>
                  @{result.type ? `${result.type}:` : ''}{result.username}
                </span>
                {result.type && (
                  <span className="text-xs bg-gray-100 dark:bg-gray-700 text-gray-600 dark:text-gray-300 px-1.5 py-0.5 rounded">
                    {result.type}
                  </span>
                )}
              </div>
              {result.name && (
                <div className="text-sm text-gray-500 dark:text-gray-400 truncate">
                  {result.name}
                </div>
              )}
            </div>
          </div>
        ))
      ) : (
        <div className="px-3 py-2 text-center text-gray-500 dark:text-gray-400">
          No results found
        </div>
      )}
      
      {results.length > 0 && (
        <div className="px-3 py-1 text-xs text-gray-400 dark:text-gray-500 border-t border-gray-200 dark:border-gray-600 bg-gray-50 dark:bg-gray-800">
          Use ↑↓ to navigate, Enter to select, Esc to close
        </div>
      )}
    </div>
  );
};

export default MentionDropdown;