import React, { useState, useEffect } from 'react';

function AdvancedAnalytics({ api }) {
  const [analytics, setAnalytics] = useState({
    overview: {},
    teams: {},
    players: {},
    matches: {},
    events: {},
    users: {}
  });
  const [loading, setLoading] = useState(true);
  const [timeRange, setTimeRange] = useState('30d');
  const [activeChart, setActiveChart] = useState('overview');

  useEffect(() => {
    fetchAnalytics();
  }, [timeRange]);

  const fetchAnalytics = async () => {
    try {
      setLoading(true);
      
      // Map frontend time ranges to backend periods
      const periodMap = {
        '7d': '7days',
        '30d': '30days',
        '90d': '90days'
      };
      
      // Use the real AnalyticsController endpoint with authentication
      try {
        const response = await api.get(`/analytics/platform?period=${periodMap[timeRange] || '30days'}`);
        const analyticsData = response.data?.data || response.data;
        
        // Transform the backend data structure to match frontend expectations
        const transformedAnalytics = {
          overview: {
            totalUsers: analyticsData.overview?.total_users || 0,
            activeUsers: analyticsData.engagement?.active_users || 0,
            newUsers: analyticsData.overview?.new_users || 0,
            retention: analyticsData.engagement?.active_user_rate || 0,
            avgSessionTime: analyticsData.engagement?.avg_session_duration || '0m',
            pageViews: analyticsData.overview?.total_news + analyticsData.overview?.total_forum_threads * 10 || 0,
            bounceRate: 100 - (analyticsData.engagement?.active_user_rate || 0),
            conversionRate: analyticsData.engagement?.conversion_rate || 0,
            totalTeams: analyticsData.overview?.total_teams || 0,
            totalPlayers: analyticsData.overview?.total_users || 0, // Using total users as proxy
            totalMatches: analyticsData.overview?.total_matches || 0,
            liveMatches: analyticsData.matches?.by_status?.live || 0,
            activeEvents: analyticsData.overview?.active_events || 0
          },
          teams: {
            totalTeams: analyticsData.teams?.total || 0,
            activeTeams: analyticsData.teams?.active || 0,
            topRegions: Object.entries(analyticsData.regions || {}).map(([region, data]) => ({
              region: region,
              count: data.teams || 0,
              percentage: Math.round((data.teams / (analyticsData.teams?.total || 1)) * 100)
            })),
            averageRating: 1000, // Default rating
            totalMatches: analyticsData.matches?.total || 0,
            performance: analyticsData.teams?.performance || []
          },
          players: {
            totalPlayers: analyticsData.overview?.total_users || 0,
            activePlayers: analyticsData.engagement?.active_users || 0,
            topHeroes: (analyticsData.heroes?.heroes || []).slice(0, 5).map(hero => ({
              hero: hero.hero?.name || 'Unknown',
              picks: hero.usage?.pick_count || 0,
              winRate: hero.usage?.win_rate || 0
            })),
            roleDistribution: Object.entries(analyticsData.heroes?.role_distribution || {}).map(([role, count]) => ({
              role: role,
              count: count,
              percentage: Math.round((count / Object.values(analyticsData.heroes?.role_distribution || {}).reduce((a, b) => a + b, 1)) * 100)
            }))
          },
          matches: {
            totalMatches: analyticsData.matches?.total || 0,
            liveMatches: analyticsData.matches?.by_status?.live || 0,
            completedMatches: analyticsData.matches?.by_status?.completed || 0,
            upcomingMatches: analyticsData.matches?.by_status?.upcoming || 0,
            averageViewers: 0, // Would need view tracking
            peakViewers: 0,
            closestMatches: analyticsData.matches?.closest_matches || []
          },
          events: {
            totalEvents: analyticsData.tournaments?.total || 0,
            liveEvents: analyticsData.tournaments?.by_status?.ongoing || 0,
            completedEvents: analyticsData.tournaments?.by_status?.completed || 0,
            totalPrizePool: '$' + (analyticsData.tournaments?.prize_pools || 0).toLocaleString(),
            totalTeamsParticipated: Math.round(analyticsData.tournaments?.avg_participants || 0)
          },
          users: {
            totalUsers: analyticsData.overview?.total_users || 0,
            newUsersToday: analyticsData.growth?.summary?.new_users_today || 0,
            activeUsersToday: analyticsData.engagement?.active_users || 0,
            topCountries: [], // Would need user country data
            topContributors: analyticsData.engagement?.top_contributors || []
          },
          growth: analyticsData.growth || {},
          trends: analyticsData.trends || {},
          systemHealth: analyticsData.system_health || {}
        };
        
        setAnalytics(transformedAnalytics);
        console.log('✅ Real analytics loaded from backend:', transformedAnalytics);
        return;
      } catch (error) {
        console.error('Failed to load platform analytics:', error);
        
        // Fallback to admin stats endpoint if analytics endpoint fails
        const statsResponse = await api.get('/admin/stats');
        const stats = statsResponse.data?.data || statsResponse.data || {};
        
        // Transform admin stats to analytics format
        const fallbackAnalytics = {
          overview: {
            totalUsers: stats.overview?.totalUsers || 0,
            activeUsers: stats.users?.active || 0,
            newUsers: stats.users?.newToday || 0,
            retention: stats.engagement?.retention || 0,
            avgSessionTime: stats.engagement?.avgSessionDuration || '0m',
            pageViews: stats.pageViews || 0,
            bounceRate: stats.engagement?.bounceRate || 0,
            conversionRate: stats.engagement?.conversionRate || 0,
            totalTeams: stats.overview?.totalTeams || 0,
            totalPlayers: stats.overview?.totalPlayers || 0,
            totalMatches: stats.overview?.totalMatches || 0,
            liveMatches: stats.overview?.liveMatches || 0,
            activeEvents: stats.overview?.activeEvents || 0
          },
          teams: stats.teams || {},
          players: stats.players || {},
          matches: stats.matches || {},
          events: stats.events || {},
          users: stats.users || {},
          growth: stats.growth || {},
          trends: stats.trends || {}
        };
        
        setAnalytics(fallbackAnalytics);
        console.log('✅ Analytics loaded from admin stats:', fallbackAnalytics);
      }
      
    } catch (error) {
      console.error('❌ Analytics error:', error);
      // Don't use demo data - show empty state instead
      setAnalytics({
        overview: {},
        teams: {},
        players: {},
        matches: {},
        events: {},
        users: {}
      });
    } finally {
      setLoading(false);
    }
  };


  const timeRanges = [
    { value: '7d', label: 'Last 7 days' },
    { value: '30d', label: 'Last 30 days' },
    { value: '90d', label: 'Last 90 days' }
  ];

  const chartSections = [
    { id: 'overview', label: 'Overview', icon: '' },
    { id: 'teams', label: 'Teams', icon: '' },
    { id: 'players', label: 'Players', icon: '' },
    { id: 'matches', label: 'Matches', icon: '' },
    { id: 'events', label: 'Events', icon: '' },
    { id: 'users', label: 'Users', icon: '' },
    { id: 'maps', label: 'Maps', icon: '🗺️' }
  ];

  const renderOverviewCharts = () => (
    <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
      <div className="card p-6 text-center bg-gradient-to-br from-blue-50 to-blue-100 dark:from-blue-950/30 dark:to-blue-900/30">
        <div className="text-3xl mb-2"></div>
        <div className="text-3xl font-bold text-blue-600 dark:text-blue-400">
          {analytics?.overview?.totalUsers?.toLocaleString() || 0}
        </div>
        <div className="text-sm text-blue-700 dark:text-blue-300">Total Users</div>
        <div className="text-xs text-green-600 dark:text-green-400 mt-1">
          +{analytics?.overview?.newUsers || 0} new
        </div>
      </div>
      
      <div className="card p-6 text-center bg-gradient-to-br from-green-50 to-green-100 dark:from-green-950/30 dark:to-green-900/30">
        <div className="text-3xl mb-2"></div>
        <div className="text-3xl font-bold text-green-600 dark:text-green-400">
          {analytics?.overview?.activeUsers?.toLocaleString() || 0}
        </div>
        <div className="text-sm text-green-700 dark:text-green-300">Active Users</div>
        <div className="text-xs text-blue-600 dark:text-blue-400 mt-1">
          {analytics?.overview?.retention || 0}% retention
        </div>
      </div>
      
      <div className="card p-6 text-center bg-gradient-to-br from-purple-50 to-purple-100 dark:from-purple-950/30 dark:to-purple-900/30">
        <div className="text-3xl mb-2"></div>
        <div className="text-3xl font-bold text-purple-600 dark:text-purple-400">
          {analytics?.overview?.pageViews?.toLocaleString() || 0}
        </div>
        <div className="text-sm text-purple-700 dark:text-purple-300">Page Views</div>
        <div className="text-xs text-gray-600 dark:text-gray-400 mt-1">
          {analytics?.overview?.bounceRate || 0}% bounce rate
        </div>
      </div>
      
      <div className="card p-6 text-center bg-gradient-to-br from-red-50 to-red-100 dark:from-red-950/30 dark:to-red-900/30">
        <div className="text-3xl mb-2"></div>
        <div className="text-3xl font-bold text-red-600 dark:text-red-400">
          {analytics?.overview?.avgSessionTime || '0:00'}
        </div>
        <div className="text-sm text-red-700 dark:text-red-300">Avg Session</div>
        <div className="text-xs text-green-600 dark:text-green-400 mt-1">
          {analytics?.overview?.conversionRate || 0}% conversion
        </div>
      </div>
    </div>
  );

  const renderContent = () => {
    switch (activeChart) {
      case 'overview':
        return renderOverviewCharts();
      case 'teams':
        return (
          <div className="card p-6">
            <h3 className="text-lg font-bold mb-4">Team Analytics</h3>
            <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
              <div className="text-center">
                <div className="text-2xl font-bold text-blue-600">{analytics?.teams?.totalTeams || 0}</div>
                <div className="text-sm text-gray-600">Total Teams</div>
              </div>
              <div className="text-center">
                <div className="text-2xl font-bold text-green-600">{analytics?.teams?.activeTeams || 0}</div>
                <div className="text-sm text-gray-600">Active Teams</div>
              </div>
              <div className="text-center">
                <div className="text-2xl font-bold text-purple-600">{analytics?.teams?.averageRating || 0}</div>
                <div className="text-sm text-gray-600">Avg Rating</div>
              </div>
              <div className="text-center">
                <div className="text-2xl font-bold text-red-600">{analytics?.teams?.totalMatches || 0}</div>
                <div className="text-sm text-gray-600">Total Matches</div>
              </div>
            </div>
          </div>
        );
      case 'players':
        return (
          <div className="space-y-6">
            <div className="card p-6">
              <h3 className="text-lg font-bold mb-4">Player Analytics</h3>
              <div className="grid grid-cols-2 md:grid-cols-3 gap-4">
                <div className="text-center">
                  <div className="text-2xl font-bold text-blue-600">{analytics?.players?.totalPlayers || 0}</div>
                  <div className="text-sm text-gray-600">Total Players</div>
                </div>
                <div className="text-center">
                  <div className="text-2xl font-bold text-green-600">{analytics?.players?.activePlayers || 0}</div>
                  <div className="text-sm text-gray-600">Active Players</div>
                </div>
                <div className="text-center">
                  <div className="text-2xl font-bold text-purple-600">
                    {analytics?.players?.topHeroes?.[0]?.picks || 0}
                  </div>
                  <div className="text-sm text-gray-600">Top Hero Picks</div>
                </div>
              </div>
            </div>
            
            <div className="card p-6">
              <h3 className="text-lg font-bold mb-4">Popular Heroes</h3>
              <div className="space-y-3">
                {(analytics?.players?.topHeroes || []).slice(0, 5).map((hero, index) => (
                  <div key={hero.hero} className="flex justify-between items-center">
                    <div className="flex items-center space-x-3">
                      <span className="text-sm font-bold text-red-600">#{index + 1}</span>
                      <span className="text-gray-900 dark:text-white">{hero.hero}</span>
                    </div>
                    <div className="flex items-center space-x-4">
                      <span className="text-sm text-gray-600">{hero.picks} picks</span>
                      <span className="text-sm font-medium text-green-600">{hero.winRate}% WR</span>
                    </div>
                  </div>
                ))}
              </div>
            </div>
            
            <div className="card p-6">
              <h3 className="text-lg font-bold mb-4">Role Distribution</h3>
              <div className="space-y-3">
                {(analytics?.players?.roleDistribution || []).map((role) => (
                  <div key={role.role} className="flex justify-between items-center">
                    <span className="text-gray-900 dark:text-white">{role.role}</span>
                    <div className="flex items-center space-x-2">
                      <div className="w-32 bg-gray-200 dark:bg-gray-700 rounded-full h-2">
                        <div 
                          className="bg-blue-600 h-2 rounded-full"
                          style={{ width: `${role.percentage}%` }}
                        />
                      </div>
                      <span className="text-sm text-gray-600">{role.percentage}%</span>
                    </div>
                  </div>
                ))}
              </div>
            </div>
          </div>
        );
      case 'maps':
        return (
          <div className="space-y-6">
            <div className="card p-6">
              <h3 className="text-lg font-bold mb-4">Map Analytics</h3>
              <div className="mb-4">
                <p className="text-sm text-gray-600">
                  Data from {analytics?.maps?.totalMatchesAnalyzed || 0} matches analyzed
                </p>
              </div>
              <div className="space-y-3">
                {(analytics?.maps?.popularMaps || []).map((map) => (
                  <div key={map.name} className="flex justify-between items-center">
                    <div className="flex items-center space-x-3">
                      <span className="text-sm font-bold text-blue-600">#{map.rank}</span>
                      <span className="text-gray-900 dark:text-white">{map.name}</span>
                    </div>
                    <span className="font-medium text-green-600">{map.plays} plays</span>
                  </div>
                ))}
              </div>
            </div>
          </div>
        );
      default:
        return renderOverviewCharts();
    }
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center py-12">
        <div className="text-center">
          <div className="loading-spinner mx-auto mb-4"></div>
          <div className="text-gray-600 dark:text-gray-400">Loading analytics...</div>
        </div>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        <div>
          <h2 className="text-3xl font-bold text-gray-900 dark:text-white"> Advanced Analytics</h2>
          <p className="text-gray-600 dark:text-gray-400">Comprehensive platform insights and metrics</p>
        </div>
        
        <select
          value={timeRange}
          onChange={(e) => setTimeRange(e.target.value)}
          className="form-input"
        >
          {timeRanges.map(range => (
            <option key={range.value} value={range.value}>
              {range.label}
            </option>
          ))}
        </select>
      </div>

      <div className="card p-4">
        <div className="flex flex-wrap gap-2">
          {chartSections.map(section => (
            <button
              key={section.id}
              onClick={() => setActiveChart(section.id)}
              className={`px-4 py-2 rounded-lg text-sm font-medium transition-colors ${
                activeChart === section.id
                  ? 'bg-red-600 text-white'
                  : 'bg-gray-100 dark:bg-gray-700 text-gray-700 dark:text-gray-300 hover:bg-gray-200 dark:hover:bg-gray-600'
              }`}
            >
              {section.icon} {section.label}
            </button>
          ))}
        </div>
      </div>

      {renderContent()}

      <div className="card p-6">
        <h3 className="text-lg font-bold text-gray-900 dark:text-white mb-4">Export Analytics</h3>
        <div className="flex flex-wrap gap-4">
          <button className="btn bg-blue-600 text-white hover:bg-blue-700">
             Export to Excel
          </button>
          <button className="btn bg-green-600 text-white hover:bg-green-700">
             Generate Report
          </button>
          <button className="btn bg-purple-600 text-white hover:bg-purple-700">
             Custom Dashboard
          </button>
        </div>
      </div>
    </div>
  );
}

export default AdvancedAnalytics;